<template>
    <c-flex align="center" justify="start" class="main__container--chollos">
        <c-box class="main__tabs-container">
            <c-flex class="chollos__configuration-container">
                <BotBox
                    v-for="bot in bots"
                    v-bind:key="bot.bot_id"
                    :name="bot.name"
                    :botId="bot.bot_id"
                />
                <c-box class="create-bot-container">
                    <CreateBotDrawer />
                </c-box>
            </c-flex>
        </c-box>
    </c-flex>
</template>

<script>
import BotBox from "../components/BotBox";
import CreateBotDrawer from "../components/CreateBotDrawer";

export default {
    components: {
        BotBox,
        CreateBotDrawer,
    },
    data() {
        return {
            bots: [],
        };
    },
    methods: {},
    activated() {
        this.$root.$emit("add_title", "Bots");
        this.bots = this.$store.state.bots;
    },
};
</script>

<style scoped>
.chollos__configuration-container {
    width: 100%;
    height: 100%;
    flex-wrap: wrap;
}

.create-bot-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 215px;
}

@media only screen and (max-width: 1200px) {
    .chollos__configuration-container {
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
}
</style>