<template>
    <div>
        <c-icon-button
            variant-color="red"
            aria-label="Crear un nuevo bot"
            icon="plus"
            size="lg"
            :isRound="true"
            @click="isOpen = true"
            :isDisabled="!available_button"
        />
        <c-modal
            :is-open="isOpen"
            :on-close="close"
        >
            <c-modal-content ref="content">
                <c-modal-header>Crear un nuevo bot 🤖</c-modal-header>
                <c-modal-close-button />
                <c-modal-body>
                    <c-form-control>
                        <c-form-label>
                            Nombre del bot
                            <c-tooltip
                                has-arrow
                                label="No te preocupes, podrás configurar tu bot más adelante! 🥳"
                                placement="top"
                                bg="blue.600"
                            >
                                <c-icon
                                    name="info-circle"
                                    size="0.9em"
                                    focusable="true"
                                />
                            </c-tooltip>
                        </c-form-label>
                        <c-input
                            ref="initialRef"
                            placeholder="Ej. ChollitoWeb"
                            v-model="name"
                        />
                    </c-form-control>
                </c-modal-body>

                <c-modal-footer>
                    <c-button variant-color="blue" mr="3" @click="createBot" :isLoading="isLoading" loadingText="Creando.. 🤖">Crear bot</c-button>
                    <c-button @click="close">Cancelar</c-button>
                </c-modal-footer>
            </c-modal-content>
            <c-modal-overlay />
        </c-modal>
    </div>
</template>

<script>
export default {
    data() {
        return { isOpen: false, name: "", available_button: false, isLoading: false};
    },
    methods: {
        open() {
            this.isOpen = true;
        },
        close() {
            this.isOpen = false;
        },
        createBot() {
            this.isLoading = true;
            this.$socket.client.emit("create_bot", { name: this.name });
        },
    },
    mounted() {
        this.$socket.client.emit("get_available_instances_count");
        this.$socket.$subscribe("set_available_instances_count", (count) => {
            if (count >= 1) {
                this.available_button = true;
            }
        });
        this.$socket.$subscribe("create_bot_status", (data) => {
            if (data.status) {
                this.$toast({
                    title: "Bot creado! 🥳",
                    description: "El bot se ha creado exitosamente.",
                    status: "success",
                    duration: 9000,
                    variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
                });
                this.$router.push({
                    name: "bot_config",
                    params: {id: data.bot_id },
                });
                this.$store.state.currentBotId = data.bot_id;
                this.$store.state.currentBotName = data.name;
            } else {
                this.$toast({
                    title: "Error! 😥 ",
                    description:
                        "Oops, ha ocurrido un error al intentar crear el bot.",
                    status: "error",
                    duration: 9000,
                    variant: "subtle", // `solid` | `subtle` | `top-accent` | `left-accent`
                });
            }
            this.isLoading = false;
            this.close();
        });
    },
};
</script>